import Vue from 'vue'
import VueRouter from 'vue-router'
import Name from '../components/Name.vue'

Vue.use(VueRouter)

const routes = [{
		path: '/name',
		name: 'Name',
		component: Name
	},
	{ 
	 path: '/description', 
	 component:() => import ('../components/Description.vue')
	},
	{ 
	 path: '/index', 
	 component:() => import ('../components/index.vue')
	},
	{ 
	 path: '/Cpu', 
	 component:() => import ('../components/Cpu.vue')
	},
	{
	 path: '/RAM', 
	 component:() => import ('../components/RAM.vue')
	},
	{
	 path: '/HistoryCpu', 
	 component:() => import ('../components/HistoryCpu.vue')
	},
]

const router=new VueRouter({
	
	routes
})

// 不加报错:
// Error: Avoided redundant navigation to current location:报错显示是路由重复，
const originalPush = VueRouter.prototype.push
   VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default router
